import VueRouter from 'vue-router'
import Home from '@/pages/Home'
import Login from '@/pages/Login'
import Search from '@/pages/Search/index.vue'
import Register from '@/pages/Register'
export default new VueRouter({
    routes:[
        {
            path:'/home',
            component:Home,
            meta:{
                show:true
            }
        },
        {
            path:'/login',
            component:Login,
            meta:{
                show:false
            }
        },
        {   
            name:'search',
            path:'/search/:keyword?',
            component:Search,
            meta:{
                show:true
            }
        },
        {
            path:'/register',
            component:Register,
            meta:{
                show:false
            }
        },
        //重定向,默认首页为Home组件
        {
            path:'*',
            redirect:'/home'
        }
    ]
})